<template>
    <div id="contain">
        <div class="head">
            <div class="header">{{logo}}</div>
        <ul class="about" v-if="flag===1">
            <li class="left">
                负责人：{{sic.charge}}
            </li>
            学科类型：
            <li class="left" v-for="(item , index) in sic.confic" :key="index">
                {{item.confic}}
            </li>
        
            <a :href="URL" :download="filename" v-if="URL"></a>
            <!-- <el-link target="_blank" :href="URL" :underline="false" style="margin-left:0%">
                <el-button size="small" type="text">附件下载</el-button>
            </el-link>
            -->
        </ul>
        <ul class="about" v-else>
            <li >
                联系人：{{cop.contacts}}
            </li>
           <li>
                联系邮箱：{{cop.mail}}
            </li> 
            需求学科：
             <li v-for="(item,index) in cop.needs" :key="index">
               {{item.needs}}
            </li>
            <a :href="URL" :download="URL" v-if="URL"></a>
           <el-link target="_blank" :href="URL" :underline="false" style="margin-left:0%">
            <el-button size="small" type="text">文件下载</el-button>
        </el-link>
        </ul>
        </div>
        
        <div class="main">
             <ul class="show_part" v-if="flag===1"> 
                  
                 <li>科研成果介绍：<br>
                    <div v-html="content.results">
                    </div>
                 </li>
                 <li v-if="content.tecNew">技术创新点：<br>
                    <div v-html="content.tecNew">
                    </div>
                 </li>
                 <li v-if="content.future">市场前景及展望：<br>
                    <div v-html="content.future">
                    </div>
                 </li>
                 <li>
                    技术成熟度：{{sic.maturity}}
                </li>
                <li>
                    联系方式
                    <div>
                        个人联系方式：{{sic.information}}<br>
                        联系方式：{{sic.contacts}}<br>
                        联系人电话：{{sic.tel}}<br>
                        联系邮箱：{{sic.mail}}
                    </div>
                        
                </li>
            </ul>
            <ul class="show_part" v-else> 
                 <li>项目介绍：<br>
                    <div v-html="content.intro">
                        
                    </div>
                 </li>
                 <li>预算：<br>
                    <div v-html="content.budget">
                    </div>
                 </li>
                 <li>需求：<br>
                    <div v-html="content.document">
                    </div>
                 </li>
            </ul>
        </div>
        <div class="btns">
            <el-button type="success" round size="small" @click="pass()">审核通过</el-button>
            <el-button type="warning" round size="small" @click="notPass()">拒绝通过</el-button>
        </div>

    </div>
</template>

<script>
export default {
    data(){
        return{
            id:'',
            content:{},
            logo:'',
            charge:'',   //负责人
            selfintro:'', //简介
            contacts:'',  //联系方法
            mail:'',
            flag:0, 
            URL:'',
            sic:{},
            cop:{},
            filename:''
        }
    },
    methods:{
        getId(){
            if( this.$route.params.sincereId){
                this.logo='';
                this.id=-1;   //清空缓存的数据
                this.id=this.$route.params.sincereId;
                
                this.flag=1;  
            }else{
                this.logo='';
                this.id=-1;   //清空缓存的数据
                this.id=this.$route.params.companyId;
                this.logo='企业需求审核';
                this.flag=2;
            }
        },
        getHTML_sci(id){
            this.$http.get('boss/readSci',{
                params:{
                    id:id
                }
            }).then(res=>{
               
                let data=res.data.data.Sci;
                this.logo = data.entryname;
                this.content.results=data.results;
                this.content.tecNew=data.tecNew;
                this.content.future=data.future;
                this.sic.maturity=data.maturity;
                this.sic.charge=data.charge;
                this.sic.contacts=data.contacts;
                this.sic.mail=data.mail;
                this.sic.information=data.information;
                this.sic.tel=data.tel;
                this.sic.confic=data.disciplines;
                this.URL=data.fileurl;

                this.filename=data.filename;
            })
        },
        getHTML_company(id){
            this.$http.get('boss/readEnter',{
                params:{
                    id:id
                }
            }).then(res=>{
                 console.log(res)
                let data=res.data.data.enterpriseneeds;
                this.logo = data.title;
                this.content.intro=data.intro;
                this.content.budget=data.budget;
                this.content.document=data.document;
                this.cop.contacts=data.contacts;
                this.cop.needs=data.needs;
                this.cop.mail=data.mail;
                this.URL=data.fileurl;

                this.filename = data.filename;
            })
        },
        handleClose(done) {
        this.$confirm('确认关闭？')
          // eslint-disable-next-line no-unused-vars
          .then(_ => {
            done();
          })
          // eslint-disable-next-line no-unused-vars
          .catch(_ => {});
       },
        pass(){
            let url;
            if(this.flag===1){
                 url='boss/sciapproval';
            }else{
                 url='boss/enterapproval';
            }
            this.$confirm('设否通过审核?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'info'
                }).then(() => {
                this.$http.get(url,{
                    params:{
                        Ids:this.id
                    }
                }).then(()=>{
                    this.$message({
                    type: 'success',
                    message: '提交成功!'
                    });
                    this.$router.go(-1);
                })

                }).catch(() => {
                this.$message({
                    type: 'warning',
                    message: '已取消'
                });          
              });
        },
        notPass(){
            let url;
            if(this.flag===1){
                 url='/scireject';
            }else{
                 url='/entereject';
            }
            this.$prompt('请输入不通过原因', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                }).then(({ value })=>{
                    this.$http.post(url,{
                        id:this.id,
                        problem:value
                    }).then(()=>{
                        this.$message({
                        type: 'success',
                        message: '提交成功'
                    });
                    this.$router.go(-1);
                   })
                }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '发送失败'
                });       
                });
        }
    },
    mounted(){
        this.getId();
        if(this.flag===1){
            this.getHTML_sci(this.id);
        }else{
            this.getHTML_company(this.id)
        }
        
    }
}
</script>

<style scoped>
#contain{
    width: 100%;
    height: 100%;
    
}
.left{
    float: left;
}
.head{
    width: 100%;
    height: 70px;
    border-bottom: 1px rgb(209, 209, 209) dashed;
}
#contain .about{
    width: 35%;
    font-size: 15px;
    display: flex;
    font-weight: 300;
}
.head ul{
    margin: 10px auto;
}
.about li{
    flex-grow: 2;
    list-style: none;
}
#contain .main{
    width: 100%;
    margin:0px auto;
    padding-bottom:0px ;
    font-size: 16px;
}
.main .show_part{
    width: 90%;
    margin: 0px auto;
}
.main li{
    font-weight:bold ;
    font-size: 18px;
    list-style: none;
    margin-top: 20px;
}
.main li > div{
    font-size: 16px;
    font-weight: 500;
    line-height: 20px;
}
#contain .main .el-button{
    float: right;
    margin-right: 35px;
    
    color: rgb(20, 13, 13);
    font-size: 16px;
}
.header{
    width: 100%;
    text-align: center;
    line-height: 25px;
    font-size: 25px;
    margin: 5px;
}

.btns{
    width: 100%;
    height: 50px;
}
.btns .el-button{
    margin:10px ;
    float: right;
}
</style>